import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {uniNavBar},
	data() {
		return {
			noticeContent: "[单行] 这是 NoticeBar 通告栏，这是 NoticeBar 通告栏，这是 NoticeBar 通告栏",
			isHide: "hide",
			videoUrl: "",
			searchInputText: '',
			villaList:[],
			apartmentList:[],
			residenceList:[],
			noQuotaList:[],
			hotShopsList:[]
		}
	}, 
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		
		uni.request({
			//TODO 替换为获取通告和视频地址
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.noticeContent = res.data[0].title;
				// this.isHide="show";
				this.videoUrl = "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4"; 
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取别墅精选文章列表失败！");
				uni.showToast({
					icon: "none", 
				    title: '获取别墅精选列表失败！',
				    duration: 2000
				});
				uni.hideLoading();
			}
		});
		
		uni.request({
			//TODO 替换为别墅精选查询
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.villaList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取别墅精选文章列表失败！");
				uni.showToast({
					icon: "none", 
				    title: '获取别墅精选列表失败！',
				    duration: 2000
				});
				uni.hideLoading();
			}
		});
		
		uni.request({
			//TODO 替换为公寓精选查询
			url: '',
			method: 'GET',
			data: {},
			success: res => {
				this.apartmentList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取公寓精选文章列表失败！");
				uni.showToast({
					icon: "none", 
				    title: '获取公寓精选列表失败！',
				    duration: 2000
				});
				uni.hideLoading(); 
			}
		});
		
		uni.request({
			//TODO 替换为住宅优选查询
			url: '',
			method: 'GET',
			data: {},
			success: res => {
				this.residenceList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取住宅优选列表失败！");
				uni.showToast({
				    icon: "none", 
					title: '获取住宅优选文章列表失败！',
				    duration: 2000
				});
				uni.hideLoading();
			}
		});
		
		uni.request({
			//TODO 替换为住宅不限购查询
			url: '',
			method: 'GET',
			data: {},
			success: res => {
				this.noQuotaList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取住宅不限购列表失败！");
				uni.showToast({
				    icon: "none",
					title: '获取住宅不限购文章列表失败！',
				    duration: 2000
				});
				uni.hideLoading();
			}
		});
		
		uni.request({
			//TODO 替换为热门商铺查询
			url: '',
			method: 'GET',
			data: {},
			success: res => {
				this.hotShopsList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取热门商铺列表失败！");
				uni.showToast({
				    icon: "none",
					title: '获取热门商铺文章列表失败！',
				    duration: 2000
				});
				uni.hideLoading();
			}
		});
	},
	onReachBottom() {
		console.log("到底了~");
	},
	methods: {
		checkin(){
			uni.showToast({
			    title: '签到成功+1',
			    duration: 2000,
				icon: "none"
			});
		},
		openlist(e){
			uni.navigateTo({
				url: '../article-list/article-list',
			});
		},
		openinfo(e){
			var article_id = e.currentTarget.dataset.article_id;
			uni.navigateTo({
				url: '/pages/article-info/article-info?article_id='+article_id,
			});
		}
	}
}
